@import url(https://fonts.googleapis.com/css?family=Merriweather);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  background: #fff;
  font-family: 'Merriweather', sans-serif;
}

h1 {
  text-align: center;
  color: #a8a8a8;
  text-shadow: 1px 1px 0 white;
}

form {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
}
form input, form textarea {
  border: 0;
  outline: 0;
  padding: 1em;
  border-radius: 8px;
  display: block;
  width: 100%;
  margin-top: 1em;
  font-family: 'Merriweather', sans-serif;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  resize: none;
}
form input:focus, form textarea:focus {
  box-shadow: 0 0px 2px #e74c3c !important;
}
form #input-submit {
  color: white;
  background: #e74c3c;
  cursor: pointer;
}
form #input-submit:hover {
  box-shadow: 0 1px 1px 1px rgba(170, 170, 170, 0.6);
}
form textarea {
  height: 126px;
}

.half {
  float: left;
  width: 48%;
  margin-bottom: 1em;
}

.right {
  width: 50%;
}

.left {
  margin-right: 2%;
}

@media (max-width: 480px) {
  .half {
    width: 100%;
    float: none;
    margin-bottom: 0;
  }
}
/* Clearfix */
.cf:before,
.cf:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}

.cf:after {
  clear: both;
}

.contact-form-bg{
    height: calc(100vh - 57px);
}

.pagination-container {
	text-align: center;
	width: 100%;
}

.pagination {
	display: inline-block;
	padding: 0;
	list-style: none;
    margin-bottom: 50px;
    justify-content: center;
}

.pagination li {
	display: inline-block;
}

.pagination li a {
	display: block;
	width: 30px;
	height: 30px;
	color: #888;
	text-decoration: none;
	line-height: 30px;
	font-size: 14px;
	text-align: center;
	margin: 0 10px;
}

.pagination li.active a {
	background: #feba39;
	border-radius: 50%;
	color: #fff;
}


* {
  box-sizing: border-box;
  font-family: 'Helvetica', sans-serif;
}

.container {
  text-align: center;
}

.csv-input {
  padding: 10px;
  display: block;
  margin: 15px auto;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.container p {
  padding-top: 10px;
  color: #666;
}

table{
  overflow: auto;  

}
